
.forceGraph_container__Tq_FT {
	width: 100%;
	height: 100%;
	position: relative;
	background-color: white;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.6);
}

.forceGraph_container__Tq_FT svg {
	display: block;
	width: 100%;
	height: 100%;
}

.forceGraph_male__2xaPq {
	fill: rgb(22, 130, 218);
}

.forceGraph_female__WoBzN {
	fill: rgb(246, 0, 0);
}

.forceGraph_node__3ELgg text {
	font: 12px sans-serif;
}

.forceGraph_labels__3VLYB > * {
	pointer-events: none;
	background-color: brown;
}

svg .forceGraph_labels__3VLYB, svg text {
	pointer-events: none;
}

div.forceGraph_tooltip__mYPCY {
	position: absolute;
	text-align: center;
	width: 110px;
	padding: 10px;
	font: 12px sans-serif;
	background: lightsteelblue;
	border: 0;
	border-radius: 8px;
	pointer-events: none;
}

:root {
  --myNavBarHeight: 120px;
  --myTableBorderColor: #6e6e6e;
  --pageHorizontalPadding: 0 100px;
  --left-bg-color: rgb(153, 153, 255);
}

html, body {
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(142, 198, 198);
  padding-top: 32px;
  display: flex;
}

/* ========================================== */
.my-navbar {
	width: 100%;
	padding: 10px;
}

.title-stuff {
	border: 2px solid red;
	color: white !important
}

/* ========================================== */
.App {
	height: 100%;
	border: 1px solid red;
	/* display: flex; */
}

.spacer {
	height: 10px;
	background-color: aquamarine;
}

.app-container {
  height: 100%;
  width: 100%;
  font-size: 0;
  display: flex;
}

.left, .middle, .right {
  display: inline-block;
  *display: inline;
  zoom: 1;
  vertical-align: top;
  font-size: 12px;
  padding: 10px;
}
.left {
  width: 30%;
  background: rgb(153, 153, 255);
  background: var(--left-bg-color);
  display: flex;
}
.middle {
  width: 40%;
  background: rgb(138, 255, 138);
}
.right {
  width: 30%;
  background: rgb(255, 255, 118);
  display: flex;
}

/* ========================================== */
table {
	background-color: rgb(153, 153, 255);
	background-color: var(--left-bg-color);
	border: 1px solid #6e6e6e;
	border: 1px solid var(--myTableBorderColor);
	/* margin: 3 auto; */
	/* border-spacing: 0; */
	/* width: 200px; */
}

thead th {
	border-bottom: 2px solid #6e6e6e;
	border-bottom: 2px solid var(--myTableBorderColor);
}

/* tfoot th {
	border-top: 2px solid var(--myTableBorderColor);
} */

tr {
	background-color: rgb(153, 153, 255);
	background-color: var(--left-bg-color);
	border-bottom: 1px solid #6e6e6e;
	border-bottom: 1px solid var(--myTableBorderColor);
	/* padding-bottom: 1px;
	padding-top: 1px; */
}

th, td {
	text-align: left;
	padding: 0.5rem;
}

/* hr {
	margin: 0;
} */

/* tbody tr:nth-child(odd) {
	background-color: #f8f8f866;
} */

thead, tbody tr {
	display: table;
	width: 100%;
	table-layout: fixed;
}

thead {
	/* display: block; */
	width: calc(100% - 1.2em);
	/* line-height: 0.5; */
}

tbody {
	display: block;
	height: 600px; /* actual size doesn't matter? just larger than screen */
	overflow: auto; /* because of this */
}

/* .myTable {
	border: 1px solid red;
} */

/* ========================================== */

section {
	border: 1px solid red;
	display: flex;
}

.my-container {
	display: flex;
}

svg {
	border: 1px solid red;
}

/* ========================================== */
::-webkit-scrollbar {
	width: 10px;
}

::-webkit-scrollbar-track {
  background: rgb(153, 153, 255);
  background: var(--left-bg-color);
  /* filter:brightness(); */
  /* opacity: 0.2; */
}

::-webkit-scrollbar-thumb {
  background: rgb(91, 91, 91);
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
